@font-face {
  font-family: 'Roboto';
  src: url("../fonts/roboto-400-normal.woff2") format('woff2'), url("../fonts/roboto-400-normal.woff") format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/montserrat-500-normal.woff2") format('woff2'), url("../fonts/montserrat-500-normal.woff") format('woff'), url("montserrat-500-normal.ttf") format('truetype');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/montserrat-600-normal.woff2") format('woff2'), url("../fonts/montserrat-600-normal.woff") format('woff'), url("montserrat-500-normal.ttf") format('truetype');
  font-weight: 600;
  font-style: normal;
}
.page {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%;
  height: 100%;
}
.page__inner {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.page__content {
  flex-grow: 1;
}
.page__footer {
  flex-shrink: 0;
  padding: 32px 0;
  background-color: #092433;
}
@media (min-width: 1168px) {
  .page__footer {
    padding: 40px 0;
  }
}
.container {
  width: 288px;
  margin: 0 auto;
}
@media (min-width: 1168px) {
  .container {
    width: 1136px;
  }
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  background-color: #fff;
  margin: 0;
  min-height: 100%;
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.4;
  color: #000;
  height: 100%;
}
[tabindex='-1']:focus {
  outline: none !important;
}
[role='button'] {
  cursor: pointer;
}
input[type='search'] {
  -webkit-appearance: textfield;
}
a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation;
}
a svg,
button svg {
  pointer-events: none;
}
img {
  max-width: 100%;
  height: auto;
}
.button {
  display: inline-block;
  padding: 16px 40px;
  border-radius: 4px;
  background: #f00;
  font: 500 16px/24px Montserrat, Arial, sans-serif;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
}
.button:hover {
  background-color: #d70000;
}
.page-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
.page-footer__logo {
  display: block;
  width: 113px;
  height: 24px;
  margin-bottom: 32px;
  background: url("../img/selectel-logo.svg") no-repeat 50%;
  transition: opacity 0.3s ease;
}
.page-footer__logo:hover {
  opacity: 0.8;
}
@media (min-width: 1168px) {
  .page-footer__logo {
    margin-bottom: 0;
  }
}
.page-footer__socials {
  display: flex;
  align-items: center;
}
.page-footer__social-link {
  display: block;
  margin: 0 10px;
  transition: opacity 0.3s ease;
}
.page-footer__social-link:hover {
  opacity: 0.8;
}
@media (min-width: 1168px) {
  .page-footer__social-link {
    margin: 0 0 0 20px;
  }
}
.page-footer__social-link--vk {
  background: url("../img/vk.svg") no-repeat 50%;
  width: 32px;
  height: 18px;
}
.page-footer__social-link--fb {
  background: url("../img/fb.svg") no-repeat 50%;
  width: 11px;
  height: 24px;
}
.page-footer__social-link--twitter {
  background: url("../img/twitter.svg") no-repeat 50%;
  width: 28px;
  height: 24px;
}
.page-footer__social-link--habr {
  background: url("../img/habr.svg") no-repeat 50%;
  width: 22px;
  height: 24px;
}
.page-footer__social-link--youtube {
  background: url("../img/youtube.svg") no-repeat 50%;
  width: 28px;
  height: 20px;
}
.page-footer__social-link--instagram {
  background: url("../img/instagram.svg") no-repeat 50%;
  width: 24px;
  height: 24px;
}
@media (min-width: 1168px) {
  .page-footer {
    flex-direction: row;
  }
}
.page-header {
  display: block;
  position: absolute;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 0;
  transition: background-color 0.3s ease;
}
.page-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page-header__logo {
  width: 113px;
  height: 24px;
  background: url("../img/selectel-logo.svg") no-repeat 50%;
  transition: opacity 0.3s ease;
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__phone {
  width: 22px;
  height: 22px;
  font-size: 20px;
  line-height: 22px;
  color: #fff;
  text-decoration: none;
  background: url("../img/phone.svg") no-repeat 0 50%;
  background-size: 22px;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  transition: opacity 0.3s ease;
}
.page-header__phone:hover {
  opacity: 0.8;
}
@media (min-width: 1168px) {
  .page-header__phone {
    width: auto;
    text-indent: 38px;
  }
}
.slider {
  position: relative;
  color: #fff;
  height: 568px;
  overflow-y: hidden;
}
.slider__slide {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: 568px;
  width: 100%;
  padding: 120px 0 75px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.6s step-end, visibility 0.6s step-end;
}
.slider__slide--active {
  z-index: 2;
  opacity: 1;
  visibility: visible;
  transition: opacity 0.6s ease, visibility 0.6s ease;
}
.slider__slide--blue {
  background: linear-gradient(180deg, #2f3f66 0%, #314579 100%);
}
.slider__slide--grey-blue {
  background: linear-gradient(180deg, #2f4966 0%, #34557c 100%);
}
.slider__slide--green {
  background: linear-gradient(180deg, #2f6066 0%, #327179 100%);
}
.slider__slide--purple {
  background: linear-gradient(180deg, #4e2f66 0%, #603285 100%);
}
@media (min-width: 1168px) {
  .slider__slide {
    height: 600px;
    padding: 136px 0 50px;
  }
}
.slider__title {
  max-width: 752px;
  margin: 0;
  padding: 0 0 32px;
  font: 600 32px/40px Montserrat, Arial, sans-serif;
}
.slider__title--vmware {
  max-width: 600px;
}
.slider__title--tier3 {
  max-width: 650px;
}
@media (min-width: 1168px) {
  .slider__title {
    font-size: 40px;
    line-height: 48px;
    padding-bottom: 40px;
  }
}
.slider__description {
  display: none;
  max-width: 576px;
  padding-bottom: 40px;
  font-size: 16px;
  line-height: 24px;
}
.slider__description--tier3 {
  max-width: 550px;
}
@media (min-width: 1168px) {
  .slider__description {
    display: block;
  }
}
.slider__dots {
  display: none;
  position: absolute;
  bottom: 40px;
  z-index: 10;
  width: 100%;
  justify-content: center;
}
@media (min-width: 1168px) {
  .slider__dots {
    display: flex;
  }
}
.slider__dot {
  width: 8px;
  height: 8px;
  margin: 0 4px;
  border: 1px solid #fff;
  border-radius: 100%;
  cursor: pointer;
  opacity: 0.6;
  transition: opacity 0.3s ease;
}
.slider__dot:hover {
  opacity: 1;
}
.slider__dot--active {
  background-color: #fff;
  opacity: 1;
}
.slider__arrows {
  width: 100%;
  position: absolute;
  bottom: 20px;
  z-index: 100;
}
@media (min-width: 1168px) {
  .slider__arrows {
    display: none;
  }
}
.slider__arrows-wrapper {
  display: flex;
}
.slider__arrow-btn {
  display: block;
  border: none;
  background: none;
  padding: 10px;
  margin: 4px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.slider__arrow-btn:hover {
  opacity: 0.8;
}
.slider__arrow-btn::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-top: 14px solid transparent;
  border-bottom: 14px solid transparent;
}
.slider__arrow-btn--prev::before {
  border-right: 28px solid #fff;
}
.slider__arrow-btn--next::before {
  border-left: 28px solid #fff;
}
.configurations {
  padding: 80px 0;
}
@media (min-width: 1168px) {
  .configurations {
    padding: 120px 0;
  }
}
.configurations__title {
  margin: 0;
  padding: 0 0 48px;
  font-family: Montserrat, Arial, sans-serif;
  font-weight: 600;
  font-size: 32px;
  line-height: 40px;
  text-align: center;
}
@media (min-width: 1168px) {
  .configurations__title {
    font-size: 40px;
    line-height: 48px;
  }
}
.configurations__headers {
  display: none;
}
@media (min-width: 1168px) {
  .configurations__headers {
    display: flex;
  }
}
.configurations__header {
  padding: 0 0 16px 42px;
  opacity: 0.8;
}
.configurations__header--cpu {
  width: 288px;
}
.configurations__header--hdd,
.configurations__header--ram {
  width: 192px;
}
.configurations__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
  padding: 24px 24px 24px 40px;
  border: 2px solid rgba(0,0,0,0.8);
  border-radius: 8px;
}
@media (min-width: 1168px) {
  .configurations__item {
    margin-bottom: 24px;
    flex-direction: row;
    align-items: center;
  }
  .configurations__item:last-child {
    margin-bottom: 0;
  }
}
.configurations__item:last-child {
  margin-bottom: 0;
}
.configurations__cell {
  padding-bottom: 16px;
  font-size: 18px;
  line-height: 24px;
}
.configurations__cell:last-child {
  padding-bottom: 0;
}
@media (min-width: 1168px) {
  .configurations__cell {
    padding-bottom: 0;
    line-height: 21px;
  }
}
.configurations__cell--cpu {
  width: 288px;
  padding-right: 115px;
}
.configurations__cell--hdd,
.configurations__cell--ram {
  width: 192px;
}
@media (min-width: 1168px) {
  .configurations__cell--order {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
  }
}
.configurations__cell-title {
  display: block;
  font-size: 16px;
  opacity: 0.8;
}
@media (min-width: 1168px) {
  .configurations__cell-title {
    display: none;
  }
}
.configurations__loader {
  display: block;
  width: 68px;
  height: 68px;
  margin: 10px auto;
  background: url("../img/loader.svg") no-repeat 50%;
  background-size: 68px 68px;
  animation: loader 1s linear infinite;
}
.configurations__error {
  font-family: Montserrat, Arial, sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  margin: 10px auto;
}
@-moz-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-o-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
